.contentBlock {
  max-width: 18.75em;
  background: #b3ebaa;
  background: linear-gradient(89.66deg, #b3ebaa 5.41%, #6bce6f 94.22%);
  border-radius: 1.5em;
  padding: 3em 2em;
}
.contentBlock .title {
  margin: 0;
  margin-bottom: 0.5em;
  text-align: center;
}
.contentBlock .content {
  margin: 0;
  padding: 0;
}
.contentBlock .button {
  margin: auto;
  display: block;
  margin-top: 1em;
  border-radius: 2em;
  padding: 1em;
}